  components: {
    quillEditor
  },
  methods: {
    onEditorChange({ editor, html, text }) {
      console.log(editor,html,text);
      this.content = html;
    },
    submit(){
      console.log(this.content);
      this.$message.success('');
    }
  }
}
</script>

